<template>
  <div class="container app-container">
    <app-paragraph :key="componentKey"></app-paragraph>
    <app-btn @action="getCurrent">Мудрая мысль</app-btn>
  </div>
</template>

<script>
import { mapMutations } from 'vuex'
import AppParagraph from '@/components/AppParagraph'
import AppBtn from '@/components/AppBtn'
export default {
  name: 'App',

  data () {
    return {
      componentKey: 0
    }
  },

  methods: {
    ...mapMutations('paragraph', ['getCurrent']),
    forceRerender () {
      this.componentKey += 1
    },

    workScroll () {
      const vh = window.innerHeight * 0.01
      document.documentElement.style.setProperty('--vh', `${vh}px`)
    }
  },

  mounted () {
    this.workScroll()
    window.addEventListener('resize', this.workScroll)
  },

  components: {
    AppParagraph,
    AppBtn
  }
}
</script>

<style scoped>
  .app-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
</style>
