import getRandom from '@/functions/random-number'
import database from '@/store/modules/database'

export default {
  namespaced: true,
  state () {
    return {
      current: null,
      paragraphs: database,
      transition: false
    }
  },

  getters: {
    current (state) {
      return state.current
    },
    transition (state) {
      return state.transition
    }
  },

  mutations: {
    getCurrent (state) {
      state.transition = true
      setTimeout(() => {
        const lastNum = state.paragraphs.length - 1
        const number = getRandom(0, lastNum)
        state.current = state.paragraphs[number]
        state.transition = false
      }, 1500)
    }
  }
}
